.navbar {
    background: white;
    height: 3.5rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;

    border-bottom: 1px solid rgb(0 0 0 / 8%);
}

.navbar-container {
    display: flex;
    align-items: center;
    height: 100%;
    width:100%;

}

.navbar-logo {
    width: 40vw;
    height: 100%;
    color: #57068C;
    padding-left: 5rem;
    cursor: pointer;
    text-decoration: none;
    font-family: 'Oleo Script', cursive;
    font-size: 2rem;
    display: flex;
    align-items: center;
}

.navbar-logo-icon {
    height: 3rem;
    width: 3rem;
    fill: #57068C;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 1rem;
    list-style: none;
    text-align: center;
    height:100%;
    width: 60vw;
    justify-content: end;
    padding-right: 2rem;
}

.nav-item {
    height: auto;
    width: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    cursor: pointer;
}

.nav-item-clicked {
    height: auto;
    width: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 2px solid #191919;
    box-sizing: border-box;
    cursor: pointer;
}

.nav-icon {
    margin-top: 0.3rem;
    height: 2rem;
    width: 2rem;
    color: #595959;

}

.nav-links {
    width:100%;
    padding-top: 0.1rem;
    font-family: 'Gothic A1', sans-serif;
    font-size: 0.8rem;
    display: flex;
    justify-content: center;
    text-decoration: none;
    font-weight: bold;
    color: #595959 !important;
}


.nav-links-clicked {
    width:100%;
    padding-top: 0.1rem;
    font-family: 'Gothic A1', sans-serif;
    font-size: 0.8rem;
    display: flex;
    justify-content: center;
    text-decoration: none;
    font-weight: bold;
    color: #191919 !important;
}

.nav-item:hover > .nav-icon{

    transition: all 0.2s ease-out;
}

.fa-bars {
    color: #fff;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #242222;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times {
        color: #fff;
        font-size: 2rem;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: #fff;
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
    }

    .nav-links-mobile:hover {
        background: #fff;
        color: #242424;
        transition: 250ms;
    }
}